// Customize Bootstrap default variables, and define custom CSS classes.

// You must control-c and re-run 'npm start' to see changes to this file.

// Currently defining custom background colors using approach in:
// https://stackoverflow.com/a/68824577/2038293
// There should be a new way as of Bootstrap 5.2

// 1. Include functions first (so you can manipulate colors, SVGs, calc, etc)
@import "../node_modules/bootstrap/scss/functions";

// 2. Change built-in variable values before loading the variables file.
$dark: #0f9391;  // Same as $green.

@import "../node_modules/bootstrap/scss/variables";

// 3. Add custom variable definitions.

// Define new variable values for custom background and text colors
// Don't forget to keep this synced with the color definitions in Theme.js!

$pf-allium-dark: $blue-300;
$pf-allium-light: $blue-100;
$pf-beetroot-dark: $indigo-300;
$pf-beetroot-light: $indigo-100;
$pf-mustard-dark: $purple-500;
$pf-mustard-light: $purple-200;
$pf-gourd-dark: $pink-300;
$pf-gourd-light: $pink-100;
$pf-legume-dark: #63595C;  // Add a brown color.
$pf-legume-light: lighten(#63595C, 20);  // Add a brown color.
$pf-misc_annuals-dark: $orange-300;
$pf-misc_annuals-light: $orange-100;
$pf-nightshade-dark: $yellow-500;
$pf-nightshade-light: $yellow-100;
$pf-umbell-dark: $green-300;
$pf-umbell-light: $green-100;
$pf-daisy-dark: $teal-500;
$pf-daisy-light: $teal-200;
$pf-misc_perennials-dark: $cyan-400;
$pf-misc_perennials-light: $cyan-200;
// OLD
//$pf-allium: #EA9D9D;
//$pf-beetroot: #B5D0F8;
//$pf-mustard: #B9FF99;
//$pf-gourd: #829DC4;
//$pf-legume: #E5AF76;
//$pf-misc_annuals: #0dcaf0;
//$pf-nightshade: #F2EDA6;
//$pf-umbell: #74BE93;
//$pf-daisy: #20c997;
//$pf-misc_perennials: #E0A2F6;

$plant-family-colors: (
  "pf-allium-dark": $pf-allium-dark,
  "pf-allium-light": $pf-allium-light,
  "pf-beetroot-dark": $pf-beetroot-dark,
  "pf-beetroot-light": $pf-beetroot-light,
  "pf-mustard-dark": $pf-mustard-dark,
  "pf-mustard-light": $pf-mustard-light,
  "pf-gourd-dark": $pf-gourd-dark,
  "pf-gourd-light": $pf-gourd-light,
  "pf-legume-dark": $pf-legume-dark,
  "pf-legume-light": $pf-legume-light,
  "pf-misc_annuals-dark": $pf-misc_annuals-dark,
  "pf-misc_annuals-light": $pf-misc_annuals-light,
  "pf-nightshade-dark": $pf-nightshade-dark,
  "pf-nightshade-light": $pf-nightshade-light,
  "pf-umbell-dark": $pf-umbell-dark,
  "pf-umbell-light": $pf-umbell-light,
  "pf-daisy-dark": $pf-daisy-dark,
  "pf-daisy-light": $pf-daisy-light,
  "pf-misc_perennials-dark": $pf-misc_perennials-dark,
  "pf-misc_perennials-light": $pf-misc_perennials-light,
);

$theme-colors: map-merge($theme-colors, $plant-family-colors);
