// 5. Define custom CSS styles

// For home page, provide color blocking for alternating sections.
.gray-background {
  background-color: $gray-200;
  padding-top: 10px;
  padding-bottom: 20px;
}

// Move page contents below fixed navbar.
html {
  scroll-padding-top: 56px;
}

// Move carousel caption to be under images.
.carousel-caption {
  position: relative;
  left: 0;
  top: 0;
  padding-bottom: 2.5rem;
}

////  HARVEST PATTERNED BACKGROUNDS FOR EACH PLANT FAMILY

.bg-pf-allium-light-harvest {
  background-color: $pf-allium-light;
  background-size: 10px;
  background-image:  repeating-linear-gradient(to right, $pf-allium-dark, $pf-allium-dark 1px, $pf-allium-light 1px, $pf-allium-light);
}

.bg-pf-allium-dark-harvest {
  background-color: $pf-allium-dark;
  background-size: 10px;
  background-image:  repeating-linear-gradient(to right, $pf-allium-light, $pf-allium-light 1px, $pf-allium-dark 1px, $pf-allium-dark);
}

